.vmdb-loadmorebtn {
  background: #71ACB1;
  width: 100%;
  min-height: 50px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s;
  border-radius: 20px;
}

.vmdb-loadmorebtn p {
  font-family: 'Abel', sans-serif;
  font-size: 42px;
  padding: 20px;
}

.vmdb-loadmorebtn:hover {
  opacity: 0.8;
}